                        <div class="ll-rg">
                            <div class="input-croll">
                                <label ><span>
                                    登录
                                </span></label>
                                <div class="input-center">
                                    <input type="text" @focus="error_common=''" placeholder="请输入账号" v-model="login_form.username">
                                </div>
