<script>
    import Axios from "../util/Request"
    import qs from "qs";
    import Api from "../api/Api";
    import Action from  "../stores/actions";
    import axios from "axios";
    export default {
        name: "Login.vue",
        data(){
            return {
              error_common:"",
              github_clientId:"0ee189d28e8bacd409cb",
              github_SecretId:"127091a6cd7f70a575d4a1b7d8c1915ec020db21",
              github_code:"",
              login_form: {
                  username:"",
                  password_err:"",
                  username_err:"",
                  password:"",

                },
                threeForm:{
                  username: "",
                    isthree:"1",
                }
            }
        },
        methods:{
            register(){
              this.$router.push({
                  path:'/register',
              })
            },
            saveNullUser(){
                // this.$store.dispatch(Action.ACTION_TOKEN_SET, "aaaaa");
                // this.$store.dispatch(Action.ACTION_USER_SET, JSON.stringify({user:"admin"}));
            },
            search_params(value) {
                let path = location.href;
                if(path.includes("code")){
                    let path_params = path.split("?")[1];
                    let path_params_list = path_params.split("&");
                    for (let i = 0; i < path_params_list.length; i++) {
                        let ppp = path_params_list[i].split("=");
                        if (ppp[0] == value) {
                            return ppp[1];
                        } else {
                            return false;
                        }
                    }
                }
            },
            loginGithub(){
                this.code = this.search_params("code");
                if(this.code){
                    Api.API_LOGOUT_GITHUB(qs.stringify({
                        client_id: this.github_clientId,
                        client_secret: this.github_SecretId,
                        code: this.code
                    })).then(res=>{
                        console.log(res.data)
                        Axios.get(`https://api.github.com/user?${res.data}`).then(ress=>{
                            if(ress.status == 200){
                                this.login_form["isthree"] = "1";
                                this.login_form["username"] = ress.data.login;
                                Api.API_LOGIN(this.login_form).then(res=>{
                                    this.$store.dispatch(Action.ACTION_TOKEN_SET, res.data.data.Token);
                                    this.$store.dispatch(Action.ACTION_USER_SET, JSON.stringify(res.data.data.user));
                                    this.$router.push({
                                        path: "/",
                                    })
                                })
                            }else{

                            }
                        })
                    })
                }
            },
            login(){
                if(this.login_form.username && this.login_form.password){
                    this.login_form["isthree"] = "0";
                    Api.API_LOGIN(this.login_form).then(res=>{
                        if(res.data.status=="200"){
                            this.$store.dispatch(Action.ACTION_TOKEN_SET, res.data.data.Token);
                            this.$store.dispatch(Action.ACTION_USER_SET, JSON.stringify(res.data.data.user));
                            this.$router.push({
                                path: "/",
                            })
                        }else{
                            this.error_common= res.data.message
                        }


                    })
                }else{
                    this.error_common = "请填写用户名或密码";
                }
            }
        },
        created() {
            this.loginGithub();
        }
    }
</script>

